"use client"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Checkbox } from "@/components/ui/checkbox"
import Link from "next/link"
import { Send, Loader2 } from "lucide-react"
import { useState, useTransition, type FormEvent } from "react"
import { joinWaitlist } from "@/app/actions/join-waitlist"
import { toast } from "sonner" // Assuming sonner is installed based on package.json

export default function WaitlistPage() {
  const [email, setEmail] = useState("")
  const [accepted, setAccepted] = useState(false)
  const [isPending, startTransition] = useTransition()
  const [successMessage, setSuccessMessage] = useState("")

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault()
    if (!email || !accepted) return

    startTransition(async () => {
      const result = await joinWaitlist(email)

      if (result.success) {
        setSuccessMessage(result.message)
        toast.success(result.message)
        setEmail("")
        setAccepted(false)
      } else {
        toast.error(result.message)
      }
    })
  }

  return (
    <div className="min-h-screen bg-[#3A57FC] px-4 py-12">
      <div className="container mx-auto max-w-6xl">
        {/* Header */}
        <div className="mb-12 text-center">
          <h1 className="mb-4 text-5xl font-normal text-white md:text-7xl">Start for Free</h1>
          <p className="text-lg text-white/90">
            Already have a KasaNow account?{" "}
            <Link href="/login" className="font-semibold text-white underline hover:no-underline">
              Log in
            </Link>
          </p>
        </div>

        {/* Signup Card */}
        <div className="mx-auto max-w-md rounded-lg bg-white p-8 shadow-xl">
          <div className="mb-6 flex items-start justify-between">
            <div>
              <h2 className="mb-2 text-2xl font-semibold text-gray-900">Join the KasaNow Waitlist</h2>
              <p className="text-base text-gray-600">Be the first to send SMS without API keys!</p>
            </div>
            <div className="flex-shrink-0">
              <Send className="h-12 w-12 text-[#3A57FC]" />
            </div>
          </div>

          {successMessage ? (
            <div className="bg-green-50 border border-green-200 text-green-700 px-4 py-6 rounded-lg text-center">
              <h3 className="text-lg font-semibold mb-2">You're in! ðŸŽ‰</h3>
              <p>{successMessage}</p>
              <Button
                variant="link"
                onClick={() => setSuccessMessage("")}
                className="mt-4 text-[#3A57FC]"
              >
                Add another email
              </Button>
            </div>
          ) : (
            <>
              {/* Google Sign Up */}
              <Button
                size="lg"
                variant="outline"
                className="mb-6 h-14 w-full border-2 border-gray-300 bg-white text-base font-semibold text-gray-700 hover:bg-gray-50"
              >
                <svg className="mr-3 h-5 w-5" viewBox="0 0 24 24">
                  <path
                    fill="#4285F4"
                    d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
                  />
                  <path
                    fill="#34A853"
                    d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
                  />
                  <path
                    fill="#FBBC05"
                    d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
                  />
                  <path
                    fill="#EA4335"
                    d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
                  />
                </svg>
                Continue with Google
              </Button>

              {/* Divider */}
              <div className="mb-6 flex items-center">
                <div className="flex-1 border-t border-gray-300"></div>
                <span className="px-4 text-sm text-gray-500">OR</span>
                <div className="flex-1 border-t border-gray-300"></div>
              </div>

              {/* Email Form */}
              <form className="space-y-6" onSubmit={handleSubmit}>
                <div>
                  <Input
                    type="email"
                    placeholder="Email"
                    className="h-14 border-2 border-gray-300 text-base focus:border-[#3A57FC]"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    disabled={isPending}
                  />
                </div>

                <Button
                  type="submit"
                  size="lg"
                  className="h-14 w-full bg-[#FF8800] text-base font-semibold text-white hover:bg-[#FF7700]"
                  disabled={!accepted || isPending}
                >
                  {isPending ? (
                    <>
                      <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                      Joining...
                    </>
                  ) : (
                    "Continue with Email"
                  )}
                </Button>

                {/* Terms */}
                <div className="flex items-start space-x-2">
                  <Checkbox
                    id="terms"
                    className="mt-1"
                    checked={accepted}
                    onCheckedChange={(checked) => setAccepted(checked === true)}
                    disabled={isPending}
                  />
                  <label htmlFor="terms" className="text-sm text-gray-600">
                    I accept the{" "}
                    <Link href="/terms" className="text-[#3A57FC] underline hover:no-underline">
                      Terms of Service
                    </Link>{" "}
                    and{" "}
                    <Link href="/privacy" className="text-[#3A57FC] underline hover:no-underline">
                      Privacy Policy
                    </Link>
                  </label>
                </div>
              </form>
            </>
          )}
        </div>

        {/* Footer */}
        <div className="mt-12 text-center text-sm text-white/80">
          <p>Â© 2025 KasaNow Ltd. Simplifying SMS communication worldwide.</p>
        </div>
      </div>
    </div>
  )
}
